
.dex-controls{background:rgba(255,0,0,0);margin:40px 15px 0 40px;height:100px;padding:5px
}
.reset{margin:15px 0 0 10px;border:0;font-family:'VT323', monospace;color:#f0f0f0;cursor:pointer;opacity:0.88;outline:0
}
.reset:active{opacity:1;color:#41b883
}
.magnification-controls{width:30px;height:95px;float:right;margin-right:25px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:justify;justify-content:space-between
}
.zoom-in,.zoom-out{padding:12px 8px;cursor:pointer
}
.zoom-in::after,.zoom-out::after{display:block;width:0;height:0;border-left:8px solid transparent;border-right:8px solid transparent;border-bottom:8px solid #f0f0f0;opacity:0.88;content:''
}
.zoom-in:hover::after,.zoom-out:hover::after{opacity:1
}
.zoom-in:active::after,.zoom-out:active::after{border-bottom:8px solid #35495e
}
.zoom-out{transform:rotateX(180deg)
}



