
#vokemenu{margin:75px 0px 0px 20px;width:100%;max-height:485px;padding:15px
}
menu{padding:0
}
menu ul{padding:0;margin:0;list-style:none
}
menu li{padding:4px 18px
}
menu li:hover{background:#c7e2c5;cursor:pointer
}
.menu-scrollbar{max-height:250px;background:#e2f4e1;border-radius:5px;opacity:0.66
}
.menu-scrollbar:hover{opacity:1
}
.pokesearch{margin-top:50px
}
.search{-webkit-appearance:none;-moz-appearance:none;appearance:none;width:100%;background:#f0f0f0;padding:8px 16px;border-radius:5px;border:2px solid transparent;opacity:0.88
}
.search:focus{outline:0;border:2px solid #35495e;opacity:1
}
.currentpoke{min-height:100px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;margin-bottom:15px;background:rgba(240,240,240,0.67);border-radius:5px
}
.currentpoke h2{font-size:2rem;font-family:'pokemon', serif;color:#35495e;margin:0
}
.dexlogo{width:190px;-ms-transform:rotate(-2deg);transform:rotate(-2deg)
}



