
@keyframes shake{
from,to{transform:rotate(0) translate3d(0, 0, 0)
}
10%,30%,50%,70%,90%{transform:rotate(-25deg) translate3d(-10px, 0, 0)
}
20%,40%,60%,80%{transform:rotate(25deg) translate3d(10px, 0, 0)
}
}
.shake{animation-name:shake;animation-timing-function:ease-out;animation-duration:2s;animation-iteration-count:infinite
}
@keyframes spin{
from{transform:rotate(0)
}
to{transform:rotate(360deg)
}
}
.spin{animation-name:spin;animation-timing-function:linear;animation-duration:2s;animation-iteration-count:infinite
}
#home{height:100%;background:#f0f0f0
}
.container{width:100%;max-width:1200px;min-height:100%;margin:0 auto;text-align:center
}
.flex{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center
}
.logo{max-width:600px;margin:2rem
}
.disabled{opacity:0.66;cursor:progress
}
.vball{width:150px
}
.load{font-family:'VT323', monospace;max-width:300px;margin:auto
}
.load p,.load a{font-size:1.33rem;color:#41b883
}
.load a{text-decoration:none
}
.load p{position:relative;top:-65px
}



