
@keyframes shake{
from,to{transform:rotate(0) translate3d(0, 0, 0)
}
10%,30%,50%,70%,90%{transform:rotate(-25deg) translate3d(-10px, 0, 0)
}
20%,40%,60%,80%{transform:rotate(25deg) translate3d(10px, 0, 0)
}
}
.shake{animation-name:shake;animation-timing-function:ease-out;animation-duration:2s;animation-iteration-count:infinite
}
@keyframes spin{
from{transform:rotate(0)
}
to{transform:rotate(360deg)
}
}
.spin{animation-name:spin;animation-timing-function:linear;animation-duration:2s;animation-iteration-count:infinite
}
#vokedisplay{min-width:50%;min-height:100%
}
.screen{width:325px;height:245px;display:-ms-flexbox;display:flex;margin:145px 0 0 46px;border-radius:8px;padding:15px;padding-right:0;font-family:'VT323', monospace
}
.screen.magnified{position:relative;z-index:100;background:#e2f4e1;border:2px solid #35495e;-ms-transform:scale(1.66) translate(45%, -15%);transform:scale(1.66) translate(45%, -15%)
}
.loading{width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.loading .vball{width:50px
}
.screen-scrollbar{max-height:215px
}
.ico{width:90px;height:90px;margin:0 15px
}
.stats{list-style:none;margin:0;padding:0
}
.stats li{padding:1px
}
.stats-scrollbar{background:transparent;padding-right:15px
}
.X{position:absolute;z-index:100;right:15px;top:5px;font-size:2rem;width:30px;height:30px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;background:#41b883;border-radius:50%;cursor:pointer;color:#35495e
}
.X:active{background:#35495e;color:#41b883
}
.X::after{display:inline-block;content:'X'
}
.bold{font-size:1.33rem
}



